"use client";

import { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import { Button } from "@/app/components/ui/button";
import { Input } from "@/app/components/ui/input";
import { Label } from "@/app/components/ui/label";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/app/components/ui/card";
import { ShieldCheck } from "lucide-react";
import toast, { Toaster } from "react-hot-toast";
import { isLoggedIn, loginAdmin } from "@/utils/auth";

const Login = () => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [isLoading, setIsLoading] = useState(false);
  const [isCheckingSession, setIsCheckingSession] = useState(true);
  const router = useRouter();

  useEffect(() => {
    if (isLoggedIn()) {
      router.replace("/dashboard");
      return;
    }
    setIsCheckingSession(false);
  }, [router]);

  const handleEmptyFields = () => {
    toast.dismiss();
    toast("Please enter your credentials", {
      icon: "⚠️",
      position: "top-right",
      style: {
        background: "#fff8eb",
        color: "#92400e",
        border: "1px solid #fed7aa",
        padding: "12px 16px",
        borderRadius: "999px",
      },
    });
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    if (!email.trim() || !password.trim()) {
      handleEmptyFields();
      return;
    }

    setIsLoading(true);

    try {
      await loginAdmin(email, password);

      toast.success("Login successful!", { position: "top-right" });
      router.replace("/dashboard");
    } catch (err) {
      if (err instanceof Error && err.message === "INVALID_CREDENTIALS") {
        toast.error("Invalid credentials. Please try again.", {
          position: "top-right",
        });
      } else {
        toast.error("Something went wrong. Please try again.", {
          position: "top-right",
        });
      }
    } finally {
      setIsLoading(false);
    }
  };

  if (isCheckingSession) {
    return null;
  }

  return (
    <div className="min-h-screen flex items-center justify-center bg-background p-4">
      <Toaster />
      <Card className="w-full max-w-md shadow-lg">
        <CardHeader className="space-y-1 text-center">
          <div className="flex justify-center mb-4">
            <div className="bg-primary/10 p-3 rounded-full">
              <ShieldCheck className="h-8 w-8 text-primary" />
            </div>
          </div>
          <CardTitle className="text-2xl font-bold">Admin Login</CardTitle>
          <CardDescription>
            Enter your credentials to access the admin panel
          </CardDescription>
        </CardHeader>
        <CardContent>
          <form onSubmit={handleSubmit} className="space-y-4">
            <div className="space-y-2">
              <Label htmlFor="email">Email</Label>
              <Input
                id="email"
                type="email"
                placeholder="admin@example.com"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                required
              />
            </div>
            <div className="space-y-2">
              <Label htmlFor="password">Password</Label>
              <Input
                id="password"
                type="password"
                placeholder="Enter password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                required
              />
            </div>
            <Button type="submit" className="w-full" disabled={isLoading}>
              {isLoading ? "Signing in..." : "Sign In"}
            </Button>

          <div className="text-xs text-muted-foreground text-center mt-4 space-y-1">
            <p>Super Admin: superadmin@example.com / super123</p>
            <p>Admin 1: admin1@example.com / admin123</p>
            <p>Admin 2: admin2@example.com / admin123</p>
          </div>
          </form>
        </CardContent>
      </Card>
    </div>
  );
};

export default Login;
